input::placeholder {
  color: #fafafa;
  font-family: 'Poppins';
}

.header__input:hover {
  box-shadow: none 0 0 0 1px rgba(52, 56, 127, 1),
    0.3em 0.3em 1em rgba(0, 0, 0, 0.3);
  transform: scale(1.01);
}

.header__icon-search {
  color: #fafafa;
  position: absolute;
  bottom: 12px;
  left: 24px;
  cursor: pointer;
}

